@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Montserrat:wght@400;500;600;700&family=Roboto:wght@400;500;700&display=swap');

*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}

html{
  font-size:10px; /* 1rem = 10px */
  scroll-behavior:smooth;
}

body{
  font-family:'Lato', sans-serif;
  background-color:#000;
  color:#fff;
}

a{
  text-decoration:none;
  cursor:pointer;
}

ul{
  list-style:none;
}

button, input{
  border:none;
  outline:none;
}

/* Navigation */
nav{
  width:100%;
  display:flex;
  justify-content:space-around;
  align-items:center;
  position:fixed;
  left:0;
  top:0;
  z-index:1;
  background-color: transparent;
}

nav.active{
  background-color:#000;
  box-shadow:5px 10px 30px rgba(255, 2, 2, .33);
}

nav .menu-btn{
  display:none;
}

nav .menu{
  display:flex;
}

.active{
  background-color:#FF1414;
  color:#fff;
  box-shadow:5px 10px 30px rgba(255, 2, 2, .33);
  transition:all linear .2s;
}

.active:hover{
  color:#FF1414;
}

nav .menu li a{
  color:#fff;
  font-size:1.4rem;
  letter-spacing:1px;
  text-transform:uppercase;
  display:inline-block;
  border-radius:5px;
  padding:10px 11px;
}

nav .menu li a:hover{
  background-color:#FF1414;
}

/* Header */
header{
  width:100%;
  height:100vh;
  position:relative;
  z-index:0;
  background:url(./img/banner.jpg) center no-repeat;
  background-size:cover;
}

header::before{
  content:'';
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:radial-gradient(ellipse at 50%, rgba(181, 8, 8, .4) 0%, rgba(0, 0, 0, .6) 100%);
  z-index:-1;
}

.header-content{
  color:#fff;
  position:absolute;
  left:50%;
  top:50%;
  transform:translate(-50%, -50%);
  text-align:center;
  font-family:'Montserrat', sans-serif;
}

.header-content > p:first-of-type{
  font-size:4rem;
  font-weight:600;
  letter-spacing:0.5px;
  text-transform:uppercase;
}

.header-content h1{
  margin:38px 0 60px;
  font-size:7.2rem;
  letter-spacing:1px;
  text-transform:uppercase;
}

.header-content h1 span{
  color:#FF1414;
}

p.details{
  margin-bottom:54px;
  font-size:2.2rem;
  text-transform:capitalize;
  letter-spacing:2px;
}

.header-btn a,
.offer-btn a{
  display:inline-block;
  font-size:2.2rem;
  text-transform:uppercase;
  color:#fff;
  background-color:#FF1414;
  padding:16px 14px;
  border-radius:5px;
  border:2px solid #FF1414;
}

.header-btn a:hover,
.offer-btn a:hover{
  border:2px solid #fff;
  background-color:transparent;
}

/* Features */
#features{
  width:100%;
  height:100vh;
  font-family:'Roboto', sans-serif;
  padding-top:160px;
  display:flex;
  flex-direction:column;
  align-items:center;
}

#features h2{
  font-size:4.8rem;
  font-weight:600;
  margin-bottom:190px;
}

.feature-blocks{
  display:flex;
  justify-content:center;
  align-content:center;
  gap:100px;
  flex-wrap:wrap;
}

.feature-block{
  background-color:#000;
  width:260px;
  height:344px;
  border:1px solid #ff1414;
  border-radius:10px;
  overflow:hidden;
  padding-top:70px;
  text-align:center;
}

.feature-img img{
  object-fit:cover;
}

.feature-text{
  margin-top:70px;
  font-size:2.4rem;
  font-weight:700;
}

/* OFFER */
#offer{
  width:100%;
  height:100vh;
  background:url(./img/offer.jpg) center no-repeat;
  background-size:cover;
  text-align:center;
  position:relative;
  font-family:'Montserrat', sans-serif;
}

.offer-info{
  position:absolute;
  left:50%;
  top:50%;
  transform:translate(-50%, -50%);
  width:670px;
}

.offer-info h2{
  font-size:7.2rem;
  text-transform:uppercase;
  font-weight:500;
}

.offer-info h2 > span{
  color:#ff1414;
}

.offer-info .details{
  margin:65px 0 28px;
  font-size:2.2rem;
  font-weight:500;
  text-transform:capitalize;
  line-height:1.1; 
}

/* About */
#about{
  display:flex;
  align-content:center;
  flex-wrap:wrap;
  justify-content:center;
}

.about-image{
  flex-basis:45%;
  display:flex;
  justify-content:center;
  padding:33px;
}

.about-image img{
  width:600px;
  height:580px;
  object-fit:cover;
  border-radius:25px;
}

.about-text{
  flex-basis:45%;
  max-width:700px;
  padding-top:190px;
}

.about-text h1{
  color:#FF1414;
  font-size:4.8rem;
  text-transform:uppercase;
  font-weight:500;
  font-family:'Montserrat', sans-serif;
}

.about-text p{
  margin:36px 0 44px;
  font-size:2rem;
  font-family:'Lato', sans-serif;
  text-transform:capitalize;
  letter-spacing:1px;
  line-height:1.2;
  font-weight:500;
}

.about-text button{
  padding:17px 15px;
  color:#fff;
  background-color:#FF1414;
  border: 2px solid #ff1414;
  border-radius:5px;
  font-size:2.2rem;
  text-transform:uppercase;
  font-weight:500;
  font-family:'Montserrat', sans-serif;
  cursor:pointer;
}

.about-text button:hover{
  border: 2px solid #fff;
  background-color: transparent;
}

/* Contact */
#contact{
  width:100%;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
}

#contact h2{
  color:#fff;
  font-family:'Montserrat', sans-serif;
  font-size:4.8rem;
  font-weight:700;
  text-transform:uppercase;
}

#contact form{
  margin:40px 0 140px;
  display:flex;
  flex-direction:column;
  width:600px;
  justify-content:center;
  align-items:center;
}

#contact form input,
#contact form textarea{
  width:100%;
  height:93px;
  border-radius:15px;
  padding:0 24px;
  background-color:rgba(51, 51, 51, .9);
  color:#ccc;
  font-size:2rem;
  font-weight:500;
  border:none;
  outline:none;
  margin:15px 0;
}

#contact form textarea{
  height:200px;
  padding-top:38px;
}

#contact form input[type="submit"]{
  background:linear-gradient(90deg, #F93737, #F70000);
  margin-bottom:0;
}