@media (max-width:1199px){
  nav{
    justify-content:space-between;
    height:78px;
    padding:0 15px;
  }

  header{
    min-height:100vh;
    height:auto;
  }

  .header-content h1{
    font-size:5rem;
  }

  #features{
    min-height:100vh;
    height:auto;
    padding:0 0 50px;
  }

  #offer{
    min-height:100vh;
    height:auto;
  }

  .offer-info{
    width:90%;
  }

  .offer-info h2{
    width:auto;
    font-size:5rem;
  }

  .about-image{
    flex-basis:90%;
    order:2;
  }

  .about-image img{
    width:100%;
    height:auto;
  }

  .about-text{
    flex-basis:90%;
    text-align:center;
  }
}

@media (max-width:1100px){
  .menu-icon{
    display:block;
  }

  .logo img{
    width:75px;
  }

  nav .menu{
    display:none;
    flex-wrap:wrap;
    position:absolute;
    top:50px;
    left:0;
    background-color:#000;
    border-bottom:2px solid #FF1414;
    width:100%;
  }

  nav .menu li{
    width:100%;
    margin:0 auto;
  }

  nav .menu li a{
    width:100%;
    display:inline-flex;
    text-align:center;
    justify-content:center;
    text-align:center;
    border-radius:0;
  }

  nav .menu-icon .nav-icon{
    background:#fff;
    display:block;
    height:2px;
    position:relative;
    width:25px;
    transition:background .3s;
    cursor:pointer;
    border-radius:2px;
  }

  nav .menu-icon .nav-icon::before,
  nav .menu-icon .nav-icon::after{
    content:'';
    position:absolute;
    background:#fff;
    width:100%;
    height:100%;
    transition:all .3s;
  }

  nav .menu-icon .nav-icon::before{
    top:-6px;
  }
  nav .menu-icon .nav-icon::after{
    top:6px;
  }

  nav .menu-btn:checked ~ .menu-icon > .nav-icon{
    background:transparent;
  }

  nav .menu-btn:checked ~ .menu-icon > .nav-icon::before{
    transform:rotate(-45deg);
    top:1px;
  }

  nav .menu-btn:checked ~ .menu-icon > .nav-icon::after{
    transform:rotate(45deg);
    top:1px;
  }

  nav .menu-btn:checked ~ .menu{
    display:block;
  }
}

@media (max-width:750px){
  header{
    padding-top:120px;
  }

  .header-content{
    width:100%;
  }

  #features{
    padding-top:50px;
  }

  #contact form{
    width:90%;
  }
}